import DropdownEditModal from "@/components/Custom/DropdownEditModal.vue"
import ActionButtons from "@/components/Custom/ActionButtons.vue"
import ExportToExcelButton from "@/components/Custom/ExportToExcelButton.vue"
import axios from "axios";

import { commonMixin } from "@/core/commonMixin";
import { axiosMixin } from "@/core/axiosMixin";
import { doubleClickMixin } from "@/core/doubleClickMixin";
import { saveChangesMixin } from "@/core/saveChangesMixin";
import { helpersMixin } from "@/core/helpersMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';

export default {
  
  components: { Widget, vSelect, DelConfirmModal, DropdownEditModal, ActionButtons, ExportToExcelButton },

  mixins: [commonMixin, axiosMixin, doubleClickMixin, saveChangesMixin, helpersMixin],
  data: function() {
    return {
      commonShit: {
        pageMainApi: 'employee',
        // rest within commonMixin
      },
      boxTwo: '',
      roleTypes: [],
      performedServices: [],
      allServices: [],
      selectedServices: [],
      form: {
        id: "",
        firstname: "",
        lastname: "",
        pesel: "",
        email: "",
        pwz: "",
        address: "",
        phone: "",
        street: "",
        house: '',
        flat: '',
        city: '',
        province: null,
        country: null,
        postalCode: '',
        birthdate: '',
        groupId: 1,
        role: null,
        account: false
      },
      selected: "",
      Sex: [],
      tableKey: 0,
      columns: [
        "emplName",
        "emplLastname",
        "emplEmail",
        "emplPZW",
        "emplAdress",
        "emplPhone",
      ],
      options: {
        hiddenColumns: [
          "emplName",
          "emplLastname",
        ],
        filterByColumn: true,
        perPage: 10,
        perPageValues: [],
        pagination: { chunk: 10, dropdown: false },
        skin: "table table-striped table-hover",
        
        
        cellClasses: {
          emplName: [
            {
              class: 'my-class',
              condition: row => true,
            }
          ]
        },
        headings: {
          emplName: "Name",
          emplLastname: "Lastname",
          emplEmail: "Email",
          emplPZW: "PZW",
          emplAdress: "Adress",
          emplPhone: "Phone",
          actions: ""
        },
        filterable: [
          "emplName",
          "emplLastname",
          "emplEmail",
          "emplPZW",
          "emplAdress",
          "emplPhone"
        ],
        sortable: [
          "emplName",
          "emplLastname",
          "emplEmail",
          "emplPZW",
          "emplAdress",
          "emplPhone"
        ],
        
      },
      zones: [],
      countries: [],
      days: [
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
                { from: [], to: [], count: 1, },
            ],
            workHours: [],
    };
  },
// ============================================================= VUEX - CACHE  =========================================
  computed: {
    ...mapGetters([
      'cacheExists',
      'getCache',
      'cacheItemsCount',
      'cacheLastUpdate',
      'cacheGetRowById',
      
    ]),
    // ...mapGetters('auth', ['user']),
  },
  methods: {
    ...mapActions([
      'cacheReset',
      'cacheFill',
      'cacheAddItem',
      'cacheDeleteItem',
      'cacheUpdateItem',
      'cacheSetUpdatedDate',
      'cacheUpdate'
    ]),
    
// ============================================================= ACTIVATING =========================================
    editRow(row) {
            var mainTableIndex;
            this.filteredList = this.$refs.mainTable.allFilteredData;
            // console.log(this.filteredList);
            this.commonShit.activeRowIndex = this.filteredList.findIndex(x => x.id === row.row.id); // not supported in IE
            mainTableIndex = this.mainList.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
            this.rowIsActivated(this.mainList, mainTableIndex);
            if (this.isClickedTwice(row.row.id)) {
                this.execDoubleClick(row, 1); // doubleClickMixin
                return;
            }
            this.rowClicked(row.row.id); // doubleClickMixin
        },
    nextRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex++;
            if (this.commonShit.activeRowIndex >= this.filteredList.length) this.commonShit.activeRowIndex = this.filteredList.length-1;
            else {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
            
        }
    },
    previousRow(table) {
        var mainTableIndex;
        if (this.commonShit.activeRowIndex !== null && this.commonShit.activeRowIndex !== undefined) {
            this.commonShit.activeRowIndex--;
            if (this.commonShit.activeRowIndex < 0) this.commonShit.activeRowIndex = 0;
            {
                mainTableIndex = table.findIndex(x => x.id === this.filteredList[this.commonShit.activeRowIndex].id);
                this.rowIsActivated(table, mainTableIndex);
            }
        }
    },
    rowIsActivated(table, index) {
        this.clearForm();
        var row = table[index];
        this.commonShit.activeRow = JSON.parse(JSON.stringify(row));
        this.commonShit.editMode = true;
        this.commonShit.activeRowId = row.id;
        this.detailsTabEnabled = true;
        this.formFill(row);
        this.deactivateAllRows(table);
        this.activateRow(table[index]);
        this.setActiveMessage(row);
        this.formCloneCreate(this.form, 100);
        // specific
        //this.customerName = row.custFirstname + ' ' + row.custLastname;
    },
// ============================================================= DELETING =========================================
    async deleteRow(rowId) {
      this.commonShit.delConfirmShow = false;
      // debugger;

      let path = this.commonShit.pageMainApi + '/' + rowId;
      // console.log(row.id);
      var result = await this.axiosDeleteData(path);
      if (this.axeman.success) {
        this.cacheDeleteItem({ name: this.commonShit.pageMainApi, id: rowId, updateDate: this.axeman.updated })
        Messenger().post(this.$t(this.commonShit.pageMainApi + ".deleted"))
      } else {
        Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
      }
          this.commonShit.editMode = false;
          this.clearForm();
    },

// ============================================================= UPDATING =========================================
    onUpdateSubmit() {
        if (this.commonShit.validationOn) {
            // console.log('validating');
            var result = this.$validator.validateAll()
            .then(valid => {
            if (valid) { 
                this.updateItem(); 
            } else 
                console.log('%cValidation failed...', 'color:red;');
            return false;
            })
            .catch(() => { return false; });
        }
        else {
            this.updateItem();
        } 
    },
    async updateItem() {
        console.log('ID: ' + this.commonShit.activeRowId);

        var data = this.mapFormData();
        var path = this.commonShit.pageMainApi + "/" + this.commonShit.activeRowId; 

        var result = await this.axiosUpdateData(path, data);
        if (this.axeman.success == true) {
            this.setActiveMessage(data);
            Messenger().post(this.$t(this.commonShit.pageMainApi + '.updated') + ': ' + this.commonShit.activeRowMessage);
            data.id = this.commonShit.activeRowId;
            this.cacheUpdateItem({ name: this.commonShit.pageMainApi, data: data, updateDate: this.axeman.updated });
            this.refreshMainTable();
            this.refreshTablePage(); // double click mixin
            this.formCloneCreate(this.form, 100);
            // this.commonShit.editMode = false;
            // this.commonShit.activeRowId = '';
            // this.clearForm();
            // this.$refs.formWizardCustomer.reset();
      } else {
            console.log(this.axeman.error);
            Messenger().post({ message: this.axeman.error, type: "error" });
      }
    },

// ============================================================= ADDING =========================================
     
    onSubmit() {
        if (this.commonShit.validationOn) {
            // console.log('validating');
            var result = this.$validator.validateAll()
            .then(valid => {
            if (valid) { 
                this.addNewItem(); 
            } else 
                console.log('%cValidation failed...', 'color:red;');
            return false;
            })
            .catch(() => { return false; });
        }
        else {
            this.addNewItem();
        } 
    },
    async addNewItem() {
      var newItem = this.mapFormData(); // employee
      // console.log(newItem);
      var result = await this.axiosAddData(this.commonShit.pageMainApi, newItem);
      if (this.axeman.success == true) {
        Messenger().post(this.$t(this.commonShit.pageMainApi + '.added'));
        // ========== ONLY CACHING, NO SERVER WORKLOAD (ASSUMING NO SIMULTANEOUS EDITING)
        this.cacheAddItem({ name: this.commonShit.pageMainApi, data: newItem, updateDate: this.axeman.updated});
        
        // ========== GET LATEST UPDATE FROM SERVER, THEN CACHE (SIMULTANEOUS EDITING)
        //this.refreshEmployeeTable();
        // ==============================================================================
        this.commonShit.editMode = false;
        this.clearForm();
     } else {
       Messenger().post({ message: this.axeman.error, type: 'error', showCloseButton: true });
     }
     this.axemanReset();
    },
// ============================================================= REFRESHING LIST =========================================
    async refreshMainTable() {
      var result = await this.axiosGetLastUpdate(this.commonShit.pageMainApi, this.axeman);
// debugger;
    if (!this.cacheExists(this.commonShit.pageMainApi)) {
        // console.log('Cache not created...');
        result = await this.axiosGetData(this.commonShit.pageMainApi, this.axeman);
        // caching
        if (this.axeman.success == true)
          this.cacheFill({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
        // this.employeeList = this.getCache(this.commonShit.pageMainApi);  
    } else if (this.cacheLastUpdate(this.commonShit.pageMainApi) !=  this.axeman.updated) {
        console.log('Cache updated: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
        // update cache
        result = await this.axiosGetUpdatedData(this.commonShit.pageMainApi, this.cacheLastUpdate(this.commonShit.pageMainApi), this.axeman);
        if (this.axeman.success == true) {
          // console.log(this.axeman.gotFromApi);
          this.cacheUpdate({name: this.commonShit.pageMainApi, data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
        }
      } else {
        console.log('Cache updated: ' + this.cacheLastUpdate(this.commonShit.pageMainApi));
        // console.log('Using cached data...');
          // this.employeeList = this.getCache(this.commonShit.pageMainApi);
      }
      this.mainList = this.formatDataForListing(this.getCache(this.commonShit.pageMainApi));
      if (!this.commonShit.editMode) this.deactivateAllRows(this.mainList);
      this.axemanReset();
    },

    formatDataForListing(data) {
      for (var i = 0; i < data.length; i++) {
          data[i].emplAdress = this.formatAddress(data[i].emplStreet, data[i].emplHouse, data[i].emplFlat, 'pl');
          // data[i].rowActive = false;
        }
        // this.deactivateAllRows(data);
        return data;
    },
    
// ============================================================= FORM HANDLING ========================================= 
    formFill(data) {
      this.form.firstname = data.emplName;
      this.form.lastname = data.emplLastname;
      this.form.email = data.emplEmail;
      this.form.pwz = data.emplPZW;
      this.form.phone = data.emplPhone;
      this.form.account = data.emplAccountenable;
      this.form.groupId = data.emplGroupId;
      this.form.pesel = data.emplPesel;
      this.form.street = data.emplStreet;
      this.form.house = data.emplHouse;
      this.form.flat = data.emplFlat;
      this.form.city = data.emplCity;
      this.form.province = data.emplProvince;
      this.form.country = data.emplCountry;
      this.form.postalCode = data.emplCityCode;
      if (data.emplBirthdate === undefined || data.emplBirthdate === null)
        this.form.birthdate = '';
      else
        this.form.birthdate = data.emplBirthdate.toString().split('T')[0];
        // this.form.role = this.roleTypes[data.emplRole].label;
        //this.form.role = this.roleTypes[data.emplGroupId-1];
    },

    clearForm(e) {
      if (e !== undefined)
        e.preventDefault();
      this.form.firstname = '';
      this.form.lastname = '';
      this.form.pesel = '';
      this.form.email = '';
      this.form.pwz = '';
      this.form.address = '';
      this.form.street = '';
      this.form.house = '';
      this.form.flat = '';
      this.form.phone = '';
      this.form.city = '';
      this.form.province = null;
      this.form.country = null;
      this.form.postalCode = '';
      this.form.birthdate = '';
      this.form.account = false;
      this.form.groupId  = 0;
      this.form.role = null;

      this.commonShit.editMode = false;
      this.form.id = "";
      this.commonShit.formClone = "";
      this.commonShit.formChangesString = "";
      
      this.$validator.reset();
      this.deactivateAllRows(this.mainList); 
    },

    mapFormData() {
      this.form.firstname = this.capitalizeFirstLetter(this.form.firstname);
      this.form.lastname = this.capitalizeFirstLetter(this.form.lastname);
      this.form.street = this.capitalizeFirstLetter(this.form.street);
      this.form.city = this.capitalizeFirstLetter(this.form.city);
      var data = {
        emplName: this.form.firstname,
        emplLastname: this.form.lastname,
        emplPesel: this.form.pesel,
        emplEmail: this.form.email,
        emplPZW: this.form.pwz,
        emplAdress: this.form.street,
        emplPhone: this.form.phone,
        emplStreet: this.form.street,
        emplHouse: this.form.house,
        emplFlat: this.form.flat,
        emplCity: this.form.city,
        emplProvince: this.form.province,
        emplCountry: this.form.country,
        emplCityCode: this.form.postalCode,
        emplBirthdate: this.form.birthdate.toString() + 'T00:00:00',
        emplAccountenable: this.form.account,
        emplGroupId: this.form.role.code,
        emplRole: this.form.role.code,
      };
      return data;
    },

    // formCloneCreate(sourceForm, delayMili) {
    //   this.formCloneClear();
    //   var self = this;
    //         setTimeout(function(){
    //             self.commonShit.formClone = JSON.parse(JSON.stringify(sourceForm));
    //         }, delayMili);
    // },
    // formCloneClear() {
    //     this.commonShit.formClone = null;
    //     this.commonShit.formChangesString = ''; 
    // },
    // formCloneCompare(sourceForm) {
    //     var noChanges = true;
    //     if (JSON.stringify(sourceForm) !== JSON.stringify(this.commonShit.formClone))
    //        noChanges = false;
    //     return noChanges;
    // },
        // for (var attr in sourceForm) {
        //   if (sourceForm[attr] !== this.commonShit.formClone[attr])
        //   console.log (sourceForm[attr] + ' / ' + this.commonShit.formClone[attr]);
        //     if (this.commonShit.formClone[attr] != sourceForm[attr]) {
        //         this.commonShit.formChangesString += this.commonShit.formClone[attr] + " / " + sourceForm[attr] + "\n";
        //         noChanges = false;
        //     }
        // }
        
    
// ============================================================= MODALS ========================================= 
    showDelConfirm(row) {
      this.commonShit.delConfirmShow = true;
      this.commonShit.delModalMessage = this.delCustomMessage(row);
      // this.$refs['delModal'].show();
    },
    hideDelConfirm() {
      this.commonShit.delConfirmShow = false;
      this.commonShit.delModalMessage = '';
    },
    showDropdownEditor(dropdown) {
            this.commonShit.dropdownToEdit = dropdown;
            this.commonShit.dropdownEditShow = true;
    },
    hideDropdownEditor() {
        this.commonShit.dropdownEditShow = false;
        this.commonShit.dropdownToEdit = '';
    },
    
// ============================================================= HEADINGS =========================================
    setTableHeadings() {
      this.options.headings.emplName = this.$t("tableHeadings.name");
      this.options.headings.emplLastname = this.$t("tableHeadings.lastname");
      this.options.headings.emplEmail = this.$t("tableHeadings.email");
      this.options.headings.emplPZW = this.$t("tableHeadings.pwz");
      this.options.headings.emplPhone = this.$t("tableHeadings.phone");
      this.options.headings.emplAdress = this.$t("tableHeadings.address");
    },
    
// ============================================================= PAGE SPECIFIC =========================================
    saveChangesCondition() {
      return (this.commonShit.editMode && this.formCloneCompare(this.form) == false);
    },
    dontSaveChanges() {
        this.formReverseChanges(this.form);
        //this.formCloneCreate(this.form, 0);
    },
    setActiveMessage(row) {  
      this.commonShit.activeRowMessage = row.emplName + ' ' + row.emplLastname;
    },
    delCustomMessage(row) {
        return row.emplName + ' ' + row.emplLastname;
    },
    async countryChanged() {
        // console.log(this.customer.custCountry);
        if (this.form.country == null) {
            this.form.province = null
            this.zones = [];
            // this.$refs.provinceSelect.disabled = true;
        }
        else {
            if (!this.cacheExists("DDL/Province_" + this.form.country)) {
                var result = await this.axiosGetDataForId("DDL/Province", this.form.country, this.zones);
                this.zones = this.zones.gotFromApi.model;
                this.cacheFill({name: 'DDL/Province_' + this.form.country, data:  this.zones, updateDate: '2019-10-10'});
                // this.$refs.provinceSelect.disabled = false;
            }
            else {
                this.zones = this.getCache('DDL/Province_' + this.form.country);
            }
        }
        // }
    },
    showMe(data) {
      console.log(data);
    },
    async getServices() {
        // console.log('= main list');
        var result = await this.axiosGetLastUpdate('servic', this.axeman);
        if (!this.cacheExists('servic')) {
            result = await this.axiosGetData('servic', this.axeman);
            // caching
            if (this.axeman.success == true)
                this.cacheFill({name: 'servic', data: this.axeman.gotFromApi, updateDate: this.axeman.updated});  
        } else if (this.cacheLastUpdate('servic') !=  this.axeman.updated) {
            console.log('Cache(' + 'servic' + ') update: ' + this.cacheLastUpdate('servic'));
            // update cache
            result = await this.axiosGetUpdatedData('servic', this.cacheLastUpdate('servic'), this.axeman);
            if (this.axeman.success == true) {
            // console.log(this.axeman.gotFromApi);
            this.cacheUpdate({name: 'servic', data: this.axeman.gotFromApi, updateDate: this.axeman.updated});
            }
        } else {
            console.log('Cache(' + 'servic' + ') update: ' + this.cacheLastUpdate('servic'));
        }
        // this.mainList = this.formatDataForListing(this.getCache('servic'));
        this.performedServices = this.getCache('servic');
        this.axemanReset();
        
          this.performedServices = this.performedServices.map(this.servicesMap);

          // var unique = [new Set(this.performedServices.map(item => item.typeId))];
          // console.log(unique);
          // console.log(this.performedServices);
          this.allServices = this.groupServices();
          // console.log(this.allServices);
    },
    groupServices() {
      var grouped = [];
      for(var i = 0; i < this.performedServices.length; i++) {
        if (!grouped.hasOwnProperty(this.performedServices[i].typeId)) {
          grouped[this.performedServices[i].typeId.toString()] = [];
        }
          grouped[this.performedServices[i].typeId.toString()].push(this.performedServices[i]);
        // grouped[service.typeId].push(service);
      }
      grouped = grouped.filter(function(element) {
        if(element != '') return element;
      });
      // console.log(grouped);
      return grouped;
    },
    servicesMap(service) {
      var serviceOption = {};
      serviceOption.text = service.servicName;
      serviceOption.value = service.id;
      serviceOption.typeId = service.servicTypeId;
      serviceOption.typeDesc = service.servicTypeDesc;
      return serviceOption;
    },
    // checkDescription(item) {
    //   return item.servicTypeDesc == 'szczepionkaa';
    // },
    onWorkHoursSubmit(e) {
        e.preventDefault();
        // this.$validator.validateAll();
        //this.addNewEmployee();
        var voorhees = [];
        var dayNumber = 0;
        for (dayNumber = 0; dayNumber < this.days.length; dayNumber++) {
            // console.log(this.days[dayNumber]);
            if (this.days[dayNumber].from.length > this.days[dayNumber].to.length)
                this.days[dayNumber].from.length = this.days[dayNumber].to.length
            for (var i = 1; i < this.days[dayNumber].from.length; i++) {
                if (this.days[dayNumber].from[i] !== "" && this.days[dayNumber].to[i] !== "" && this.days[dayNumber].from[i] !== undefined && this.days[dayNumber].to[i] !== undefined) {
                    voorhees.push({ "EmpId" : this.commonShit.activeRowId, "WeekDayId" : dayNumber + 1, "DateFrom": "1900-01-01 " + this.days[dayNumber].from[i] + ":00", "DateTo": "1900-01-01 " + this.days[dayNumber].to[i] + ":00"});
                }
            }
        }
        voorhees = JSON.stringify(voorhees);
        console.log(voorhees);
        if (voorhees.length > 2) {
            console.log('saving');
            this.addJSON(voorhees, "");
        }
    },
    async addJSON(jsonData, jsonType) {
            // var newItem = {json: jsonData, jsonType: jsonType}
            var newItem = {json: jsonData}
            var result = await this.axiosAddData('employee/EmployeeOfficeOpenHour', newItem);
            if (this.axeman.success) {
                Messenger().post(this.$t('common.saveSuccessful'));
            }
            else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            this.axemanReset();
        },
    resetWorkHours() {
        for (var i = 0; i < this.days.length; i++) {
            this.days[i].from = [];
            this.days[i].to = [];
            this.days[i].count = 1;
        }
    },

    
    
  },

  // ============================================================= LIFECYCLE HOOKS =========================================  
  async created() {
    
    // debugger;
    let path = "";
    let route = this.$route.params.id;
    axios.defaults.headers.common["Authorization"] = "Bearer " + localStorage.token;

    // if (route !== undefined && route !== null) {
    //   path = this.commonShit.pageMainApi + "/" + route;

    //   axios
    //     .get(path)
    //     .then(resp => {
    //       // console.log(resp.data);
    //       this.mainList = resp.data;
    //       this.employeeFormFill(this.mainList);
    //     })
    //     .catch(error => console.log(error));
    // }
    this.setTableHeadings();
    this.refreshMainTable();
    
    if (!this.cacheExists("DDL/EmployeeRole")) {
      var result = await this.axiosGetData("DDL/EmployeeRole", this.roleTypes);
      this.roleTypes = this.roleTypes.gotFromApi.model;
      this.cacheFill({name: 'DDL/EmployeeRole', data:  this.roleTypes, updateDate: '2019-10-10'});
    }
    else {
      this.roleTypes = this.getCache('DDL/EmployeeRole');
    }
    if (!this.cacheExists("DDL/Country")) {
      result = await this.axiosGetData("DDL/Country", this.countries);
      this.countries = this.countries.gotFromApi.model;
      this.cacheFill({name: 'DDL/Country', data:  this.countries, updateDate: '2019-10-10'});
    }
    else {
      this.countries = this.getCache('DDL/Country');
    }
    this.getServices(); 
    // console.log(this.getCache('DDL/EmployeeRole'));
  },
  updated() {
    this.setTableHeadings();
  }
};
</script>

